.about {
  background-color: $primary-background-color;
  @media screen and (min-width: 320px) {
    padding-top: 125px;
  }

  .background {
    background-color: $primary-background-color;
    @media screen and (min-width: 320px) and (max-width:767px) {
      background-image: url(../images/milk-left-m@1x.png),
        url(../images/milk-right-m@1x.png);
        @media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
            background-image: url("../images/milk-left-m@2x.png"), url("../images/milk-right-m@2x.png");
        }
      background-repeat: no-repeat;
      background-position: left, right;
      background-size: contain;
    }
    @media screen and (min-width: 1280px) {
        background-image: url(../images/milk-left-d@1x.png),
          url(../images/milk-right-d@1x.png);
          @media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
              background-image: url("../images/milk-left-d@2x.png"), url("../images/milk-right-d@2x.png");
          }
        background-repeat: no-repeat;
        background-position: left, right;
        background-size: contain;
      }
  }
  
.about__text{
    font-family: Titan One;
    font-size: 18px;
    line-height: 1.17;
    letter-spacing: 0.04em;
    text-align: center;
  
    margin-bottom: 15px;
    color:$label-text-color;
  
    @media screen and (min-width: 768px) {
      font-size: 26px;
      line-height: 1.15;
    }
  
    @media screen and (min-width: 1280px) {
      font-size: 30px;
      line-height: 1.13;
    }

  }
  @media screen and (min-width: 1280px) {
    padding-top: 125px;
  }
}
.background {
  background-color: $primary-background-color;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    background-image: url(../images/milk-left-m@1x.png), url(../images/milk-right-m@1x.png);
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url('../images/milk-left-m@2x.png'), url('../images/milk-right-m@2x.png');
    }
    background-repeat: no-repeat;
    background-position: left, right;
    background-size: contain;
  }
  @media screen and (min-width: 1280px) {
    background-image: url(../images/milk-left-d@1x.png), url(../images/milk-right-d@1x.png);
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url('../images/milk-left-d@2x.png'), url('../images/milk-right-d@2x.png');
    }
    background-repeat: no-repeat;
    background-position: left, right;
    background-size: contain;
  }
}

.about__text {
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.17;
  letter-spacing: 0.04em;
  text-align: center;

  margin-bottom: 15px;
  color: $label-text-color;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.about__title {
  font-size: 34px;
  line-height: 1.2;
  text-align: center;
  font-family: Titan One;
  letter-spacing: 0.06em;
  margin-bottom: 26px;

  color: $title-text-color;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
    margin-bottom: 50px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;

    margin-bottom: 55px;
  }
}

.about__img {
  display: block;
  max-width: 280px;
  margin-bottom: 46px;
  @media screen and (min-width: 768px) {
    max-width: 340px;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1280px) {
    max-width: 516px;
  }
}

.about__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.about-desc {
  margin-left: auto;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin: 0 auto;
  }

  @media screen and (min-width: 768px) {
    max-width: 340px;
    padding-top: 39px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 463px;
    padding-top: 47px;
  }
}

.about-text-one {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;

  margin-bottom: 35px;
  color: #000000;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 220px;
  }

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.75;
    letter-spacing: 0.04em;
  }
}

.about-text-two {
  font-family: DM Sans;
  font-style: normal;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.62;
  letter-spacing: 0.04em;
  margin-bottom: 1em;
  color: $primary-text-color;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 220px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 1.76;

    @media screen and (min-width: 1280px) {
      margin-bottom: 36px;
      font-size: 16px;
      line-height: 1.75;
      letter-spacing: 0.04em;
    }
  }
}

.about-text-two-indent {
  font-family: DM Sans;
  font-style: normal;
  font-size: 12px;
  margin-bottom: 40px;
  font-weight: 500;
  line-height: 1.62;
  letter-spacing: 0.04em;

  color: $primary-text-color;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 220px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 62px;
    font-size: 14px;
    line-height: 1.76;

    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.75;
      letter-spacing: 0.04em;
      margin-bottom: 40px;
    }
  }
}
.about-modal__open {
  display: flex;
  justify-content: space-evenly;

  width: 176px;
  height: 44px;
  background: #ffffff;
  border-radius: 22px;
  align-items: center;
  text-align: center;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.3;
  color: $title-text-color;
  transition: $function-transition;
  @media screen and(min-width:480px) {
    width: 176px;
    height: 44px;
  }
}
.about-modal__open:focus,
.about-modal__open:hover {
  background: $first-accent-color;
}
.about-modal-icon {
  fill: $title-text-color;
}



// modal //
.backdrop {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  position: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  overflow-y: scroll;
}
.backdrop.is-about {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.backdrop.is-about.about-modal {
  transform: translate(-50%, -50%);
}

.about-modal {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform $function-transition;
  
  width: 100%;
  height: 100%;

  background-color: $secondary-background-color;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  
  @media screen and (min-width: 480px) {
    max-width: 380px;
  }
  @media screen and (min-width: 768px) {
    max-width: 500px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 800px;
    height: 75%;
  }
}

.about-modal__close {
  position: absolute;
  top: 25px;
  right: 25px;
  width: 110px;
  height: 25px;
  border-radius: 25px;
  cursor: pointer;
  background: $title-text-color;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: $function-transition;
  transform-style: preserve-3d;
}
.about-modal__close::after {
  top: -100%;
  left: 0;
  width: 100%;
  position: absolute;
  background: $primary-text-color;
  border-radius: 5px;
  color: #ffffff;
  content: 'Click';
  transform-origin: left bottom;
  transform: rotateX(90deg);

}
.about-modal__close:hover {
  transform-origin: center bottom;
  transform: rotateX(-90deg) translateY(100%);
}

.about-form {
  margin: 50px;
}

.about-form__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.4;
  margin-top: 40px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  margin-bottom: 15px;
  color: #FFFFFF;
  @media screen and (min-width: 480px) {
    font-size: 22px;
    line-height: 1.4;
    margin-top: 40px;
  }
  @media screen and (min-width: 768px) {
    font-size: 30px;
    line-height: 1.1;
  }

}
.about-form__text{
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 38px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color:$title-text-color;
    @media screen and (max-width:479px){
        font-size: 18px;
        line-height: 1.4; 
    }
    @media screen and(min-width:480px) and (max-width:767px){
        font-size: 22px;
        line-height: 1.4; 
    }
}

.about-form__advertising {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $title-text-color;
  @media screen and (max-width: 479px) {

    font-size: 12px;
    line-height: 16px;
    width: 290px;
    margin-bottom: 15px;
  }
  @media screen and(min-width:480px) and (max-width: 767px) {
    font-size: 20px;
    line-height: 1.2;
    width: 480px;
    margin-bottom: 15px;
  }
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 194%;
  }
}
.about-form__advertising--fatty {
  color: $secondary-text-color;
  margin-bottom: 15px;
}
.about-form-box {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-top: 20px;
  @media screen and (min-width: 1280px) {
    justify-content: space-around;
    flex-wrap: nowrap;
  }
}
.about-form-foto {
  max-width: 290px;
  max-height: 320px;
  border-radius: 24px;

  @media screen and (min-width: 768px) {
    margin-left: 20px;
    max-width: 400px;
    max-height: 450px;
  }
}
