.footer-section {
  padding-top: 113px;
  background-color: $primary-background-color;
}
.footer-text {
  max-width: 230px;
  @media screen and (min-width: 768px) {
    max-width: 340px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 100%;
  }
}
.text-one,
.text-two {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  color: $label-text-color;
  margin-bottom: 17px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
  }
}
.text-two {
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  color: $title-text-color;
  margin-bottom: 43px;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 55px;
    letter-spacing: 0.06em;
    margin: 0;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 66px;
  }
}
.list-button {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.button-link {
  @extend %position-center;
  margin-right: 28px;
}
.button-link:last-child {
  margin-right: 0;
}
.social-link-btn {
  @extend %position-center;
  background-color: $label-text-color;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  transition: background-color, $function-transition;
}
.button-icon {
  @extend %position-center;
  transition: background-color, $function-transition;
}
.social-link-btn:hover,
.social-link-btn:focus,
.button-icon:hover,
.button-icon:focus {
  background-color: $title-text-color;
}
.button-link {
  transition-property: transform;
}
.icon-contact:hover .button-link {
  transform: rotate(-360deg) scale(1.5);
}
.button-link:nth-child(1) {
  transition-property: transform;
  transition-duration: 1000ms;
}
.button-link:nth-child(2) {
  transition-property: transform;
  transition-duration: 2000ms;
}
.button-link:nth-child(3) {
  transition-property: transform;
  transition-duration: 3000ms;
}
.contact {
  display: flex;
  justify-content: flex-end;
  margin: 46px 0 43px 0;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 186%;
  text-align: right;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}
.phone {
    padding-left: 5px;
    color: $title-text-color;
}
.footer-year {
  display: flex;
  justify-content: center;
  border-width: auto;
}
.footer-year::before {
  content: '';
  position: absolute;
  display: inline-block;
  width: 100%;
  outline: 1px solid $border-color;
}
.footer-year-text {
  padding: 22px 0;
  max-width: 145px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 152%;
  text-align: center;
  color: $primary-text-color;
  @media screen and (min-width: 768px) {
    padding: 30px 0 20px 0;
    max-width: 100%;
    line-height: 186%;
    letter-spacing: 0.02em;
  }
  @media screen and (min-width: 1280px) {
    padding: 10px 0;
  }
}
.footer-position {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    padding-bottom: 90px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 100px;
  }
}
