.products {
  padding: 118px 0 98px 0;
  @media screen and (min-width: 768px) {
    padding-bottom: 110px;
  }
  @media screen and (min-width: 1280px) {
    padding: 130px 0 120px 0;
  }
}
.products__headind-label {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: $label-text-color;
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 30px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
    letter-spacing: 0.04em;
  }
}
.products__heading {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $title-text-color;
  margin-bottom: 144px;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 55px;
    margin-bottom: 150px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    margin-bottom: 260px;
    line-height: 66px;
    letter-spacing: 0.06em;
  }
}
.product-menu__item__text {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  padding-top: 160px;
  color: #ffffff;
  @media screen and (min-width: 768px) {
    padding-top: 152px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 163px;
  }
}
.products-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  //   padding-top: 144px;
  @media screen and (min-width: 768px) {
    margin: -10px;
    // padding-top: 150px;
  }
  @media screen and (min-width: 1280px) {
    margin: -15px;
    // padding-top: 260px;
  }
}
@media screen and (min-width: 0px) and (max-width: 767px) {
  .products-menu__item:not(:first-child) {
    margin-top: 130px;
  }
}
.products-menu__item {
  position: relative;
  max-width: 280px;
  border-radius: 24px;
  padding-bottom: 40px;
  @media screen and (min-width: 768px) {
    flex-basis: calc((100% - 60px) / 3);
    margin: 10px;
  }
  @media screen and (min-width: 1280px) {
    flex-basis: calc((100% - 90px) / 3);
    margin: 15px;
    max-width: 100%;
  }
}
.products-item__block {
  max-width: 330px;
}
.product-menu__item__text::after {
  content: '...';
  display: block;
  padding-top: 20px;
}
.ice-cream {
  background: $secondary-background-color;
}
.ice-coffee {
  background-color: $second-product-color;
}
.milkshakes {
  background-color: $third-product-color;
}
.ice-cream::before {
  content: url('/images/product-1m-249@1x.png');
  position: absolute;
  bottom: 100%;
  transform: translateY(50%);
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .ice-cream::before {
      content: url('/images/product-1m-498@2x.png');
    }
  }
  @media screen and (min-width: 768px) {
    content: url('/images/product-1t-220@1x.png');
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .ice-cream::before {
        content: url('/images/product-1t-440@2x.png');
      }
    }
  }
  @media screen and (min-width: 1280px) {
    content: url('/images/product-1d-335@1x.png');
    transform: translateY(40%);
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .ice-cream::before {
        content: url('/images/product-1d-670@2x.png');
      }
    }
  }
}
