.card-text {
  font-weight: bold;
  font-size: 14px;
  line-height: 152%;
  text-align: center;
  letter-spacing: 0.04em;
  padding: 0 40px;
  margin-top: 25px;
  color: white;
  @media screen and (min-width: 768px) {
    padding: 0 11px;
    line-height: 176%;
  }
  @media screen and (min-width: 1280px) {
    padding: 0 44px;
    font-size: 16px;
  }
}
@media screen and (min-width: 768px) {
  .text-non {
    display: none;
  }
}
.box {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 25px;
  border: 0px solid;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: white;
  @media screen and (min-width: 768px) {
    margin-top: 40px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 62px;
  }
}
.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: $title-text-color;
}
